        $('[loading="lazy"]').depotLazy();
        $.depotProto.$DOCUMENT.on('lazy:added', function (event, settings) {
            var $target = $(event.target);
            var $lazy;

            if ($target.is('[loading="lazy"]')) {
                $lazy = $target;
            } else {
                $lazy = $target.find('[loading="lazy"]');
            }

            if ($lazy && $lazy.length) {
                $lazy.depotLazy(settings);
            }
        });

//===========================
